var loginAjax = function (){
    var loginForm = $("#login");
    if (loginForm.length > 0){
        loginForm.find("button").on("click",function (){
            $.ajaxSetup({
                headers: {
                    'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
                }
            });

            $.ajax({
                url: '/login',
                type: 'POST',
                data: loginForm.serialize(),
                success: function (data) {
                    switch (data) {
                        case "username_error":
                            Swal.fire({
                                icon: 'error',
                                title: 'Hata...',
                                text: 'Bu kullanc ad kullanlmyor.'
                            })
                            break;
                        case "password_error":
                            Swal.fire({
                                icon: 'error',
                                title: 'Hata...',
                                text: 'ifre hatal.'
                            })
                            break;
                        case "1":
                            Swal.fire({
                                icon: 'success',
                                title: 'Baarl!',
                                text: 'Baaryla giri yaptnz, ynlendiriliyorsunuz.'
                            });

                            setTimeout(function (){
                                location.reload();
                            },1500);
                            break;
                        default:
                            Swal.fire({
                                icon: 'error',
                                title: 'Hata...',
                                text: 'Bilinmeyen bir hata olutu, ltfen yneticiler ile iletiime geiniz.'
                            })
                            break;
                    }
                }
            });

        })
    }
}
