var logoutAjax = function (){
    var logoutBtn = $("#logout");
    if (logoutBtn.length > 0){
        logoutBtn.on("click",function (){

            $.ajaxSetup({
                headers: {
                    'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
                }
            });

            $.ajax({
                url: '/logout',
                type: 'POST',
                data: {logout:1},
                success: function (data) {
                    switch (data) {
                        case "1":
                            Swal.fire({
                                icon: 'success',
                                title: 'Baarl!',
                                text: 'Baaryla k yaptnz, ynlendiriliyorsunuz.'
                            });
                            setTimeout(function (){
                                location.reload();
                            },1500);
                            break;
                        default:
                            Swal.fire({
                                icon: 'error',
                                title: 'Hata...',
                                text: 'Bilinmeyen bir hata olutu, ltfen yneticiler ile iletiime geiniz.'
                            })
                            break;
                    }
                }
            });

        })
    }
}

$(document).ready(function (){
    loginAjax();
    registerAjax();
    logoutAjax();
})
